.mobile-container {
  max-width: 400px;
  border: 1px solid #eee;
  padding: 10px;
  margin: 10px auto;
  display: block;
  background-color: white;
}
.wrap-word {
  word-wrap: break-word;
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.display-none {
  display: none !important;
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}
.link {
  color: #1890ff;
  text-decoration: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  transition: color 0.3s;
  text-decoration-skip: objects;
}
.avatar-large {
  border-radius: 50%;
  width: 120px;
  height: 120px;
  cursor: pointer;
}
.avatar-middle {
  border-radius: 50%;
  width: 80px;
  height: 80px;
  cursor: pointer;
}
.avatar-small {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  cursor: pointer;
}
@media (min-width: 992px) {
  .visible-mobile {
    display: none !important;
  }
  .visible-pc {
    display: block !important;
  }
}
@media (max-width: 992px) {
  .visible-mobile {
    display: block !important;
  }
  .visible-pc {
    display: none !important;
  }
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-thumb {
  transition: all 0.3s ease;
  border-color: transparent;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 40;
}
::-webkit-scrollbar-thumb:hover {
  transition: all 0.3s ease;
  background-color: rgba(0, 0, 0, 0.15);
}
::-webkit-scrollbar-corner {
  background-color: #eaeaeb;
}
